.navbar-wraper {
  display: flex;
  align-content: center;
  justify-content: space-between;
  backdrop-filter: blur(10px);
  position: sticky;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.moon {
  font-size: 20px;
  margin: 0 10px;
}

.sun {
  font-size: 25px;
  margin-left: -15px;
}

.border {
  border-bottom: solid;
  border-image: linear-gradient(to right, darkblue, darkorchid) 2;
  border-width: 4px;
}

@media screen and (max-width: 600px) {
  .btn {
    position: absolute;
    right: 5px;
    top: 10px;
  }
}
